@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Potta+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#thisday-maindiv {
  background-color: rgba(73, 73, 73, 0.274);
  box-shadow: 0 14px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-radius: 25px;
  width: 70vw;
  margin: auto;
  height: auto;
  display: flex;
  justify-content: center;
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  font-family: "Montserrat", sans-serif;
}

#thisday-data-container {
  margin: auto;
  color: white;
  text-align: center;
  display: flex;
}

#today-plus-city {
  width: 20vw;
  text-align: start;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

#today-plus-city #city-and-date {
  text-align: center;
  line-height: 55px;
}

#today-plus-city #city-and-date #city {
  font-size: 2.9vw;
}

#today-plus-city #city-and-date #thisday-date {
  font-size: 1.3vw;
}

#today-plus-city #logo-and-degrees {
  width: 20vw;
  height: 50%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#today-plus-city #logo-and-degrees #main-degrees {
  font-size: 3.2vw;
  margin: 5%;
}

#today-plus-city #logo-and-degrees img {
  max-height: 100px;
  max-width: 150px;
  border-radius: 15px;
  margin-right: 5%;
}

#input-plus-extraData {
  width: 50vw;
  height: auto;
}

#input-plus-extraData #input-container {
  border: none;
  margin-top: 7%;
  margin-bottom: 5%;
  margin-left: 70px;
  display: flex;
  flex-direction: column;
  line-height: 45px;
}

#input-plus-extraData #input-container p {
  font-size: 0.85vw;
  color: #cccbcb;
}

#input-plus-extraData #input-container #input-id {
  color: white;
  width: 80%;
  height: auto;
  text-align: start;
  font-size: 1.40vw;
  text-align: center;
  font-family: "Acme";
  border: solid 1px #a0b3b0;
  background: none;
  border-radius: 0px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  border-radius: 15px;
}

#input-plus-extraData #input-container #input-id:focus {
  font-weight: bold;
  letter-spacing: 1px;
}

#input-plus-extraData #input-container button {
  width: 15%;
  height: auto;
  font-size: 1vw;
  margin: auto;
  padding: 6px;
  border: none;
  border-radius: 15px;
  border: solid #a0b3b0 1px;
  color: #4d4c4c;
  font-family: "Acme";
  background-color: rgba(255, 255, 255, 0);
  outline: none;
}

#input-plus-extraData #input-container button:hover {
  transition-duration: 0.5s;
  color: white;
  background-color: #e2ec53b9;
  cursor: pointer;
  border: solid transparent 1px;
}

#extra-data {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 15%;
  text-align: center;
}

#extra-data #sunrise-sunset p,
#extra-data #wind-rain p,
#extra-data #highest-lowest p {
  font-size: 1.1vw;
  margin: 10px;
  line-height: 35px;
}

#extra-data #sunrise-sunset p p,
#extra-data #wind-rain p p,
#extra-data #highest-lowest p p {
  font-size: 1.9vw;
  color: #3a3a3a;
}

::-webkit-input-placeholder {
  color: #cacaca;
}

:-ms-input-placeholder {
  color: #cacaca;
}

::-ms-input-placeholder {
  color: #cacaca;
}

::placeholder {
  color: #cacaca;
}

:hover::-webkit-input-placeholder {
  color: #e6e6e6;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

@media (max-width: 800px) {
  #thisday-maindiv {
    width: 95vw;
  }
  #today-plus-city {
    display: flex;
    justify-content: flex-start;
  }
  #today-plus-city #city-and-date #city {
    font-size: 25px;
    margin-top: 3vh;
  }
  #today-plus-city #city-and-date #thisday-date {
    font-size: 12px;
    line-height: 15px;
  }
  #today-plus-city #logo-and-degrees #main-degrees {
    font-size: 22px;
  }
  #today-plus-city #logo-and-degrees img {
    height: 50px;
    width: 50px;
  }
  #input-plus-extraData {
    width: 60vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  #input-plus-extraData #input-container {
    width: 95%;
    margin-left: 8vw;
    margin-top: 5vh;
  }
  #input-plus-extraData #input-container p {
    font-size: 10px;
    color: #cccbcb;
    line-height: 35px;
  }
  #input-plus-extraData #input-container #input-id {
    font-size: 15px;
  }
  #input-plus-extraData #input-container button {
    padding: 5px;
    font-size: 12px;
    width: 80px;
    color: #e4e4e4;
  }
  #input-plus-extraData #extra-data {
    margin-top: 5vh;
    margin-right: 20vw;
    text-align: center;
  }
  #input-plus-extraData #extra-data #sunrise-sunset p,
  #input-plus-extraData #extra-data #wind-rain p,
  #input-plus-extraData #extra-data #highest-lowest p {
    font-size: 11px;
    line-height: 15px;
  }
  #input-plus-extraData #extra-data #sunrise-sunset p p,
  #input-plus-extraData #extra-data #wind-rain p p,
  #input-plus-extraData #extra-data #highest-lowest p p {
    font-size: 15px;
    color: #3a3a3a;
  }
  .Header {
    font-size: 15px;
  }
}
