* {
	margin: 0;
	padding: 0;
}

body {
	background-image: url("https://www.wallpapertip.com/wmimgs/173-1739030_wallpaper-hd-cloudy-weather-unique-hd-wallpapers-hd.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	min-height: 100vh;
	position: relative;
}

.Header {
	color: #fff;
	font-size: 45px;
	text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 70px rgb(253, 255, 255);
	letter-spacing: 1px;
	white-space: pre-wrap;
	text-align: center;
	margin-bottom: 4vh;
	font-family: 'Potta One', cursive;
}


input {
	width: 60%;
	padding: 10px;
	margin: auto;
	border-radius: 15px;
}

#Footer {
	width: 100%;
	position: absolute; 
	bottom:0;
	bottom: 0;
	text-align: center;
	color: rgb(216, 216, 216);
	text-align: center;
	font-family: "Acme";

}

@media(max-width: 800px) {
   


	.Header {
		position: relative;
		text-align: center;
		font-size: 33px;
		margin-bottom: 5vh;
		margin-top: 5vh;
	}
	#Footer {
	 margin-top: 5vh;
	
	}
} 



